.glide {
  position: relative;
}

.glide__arrow {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  padding: 0.5rem;
}
.glide__arrow--left {
  left: -1rem;
}
.glide__arrow--right {
  right: -1rem;
}

.glide__bullets {
  text-align: center;
}

.glide__bullet {
  @apply w-2 h-2 mx-1.5;
  background-color: gray;
  border-radius: 999px;
}
.glide__bullet.glide__bullet--active {
  background-color: black;
}

