.wcb-testimonials__item {
  display: flex;
  position: relative;
}

.wcb-testimonials__item-image {
  display: inline-flex;
  align-items: flex-start;
  flex-shrink: 0;
}

.wcb-testimonials__item-user {
  display: inline-flex;
  align-items: center;
}